.navbar{
    display: flex;
    justify-content: space-around;
    box-shadow: 0 1px 3px -2px black;

}
.shopimg{
    width: 90px;
    height: 90px;
}
.cartimg{
    width: 60px;
    height: 60px;
}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color: black;
    font-size: 38px;
    font-weight: 600;

}
.navmenu{
    display: flex;
    align-items: center;
    list-style: none;
    gap:50px;
    color: grey;
    font-size: 20px;
    font-weight: 500;


}
.navmenu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap:3px;
    cursor: pointer;
}
.navmenu hr{
    border:none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: red;
}
.navcart{
    display: flex;
    align-items: center;
    gap: 45px;

}
.navcart button{
    width:157px;
    height: 58px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 20px;
    font-weight: 500;
    background: white;
    cursor: pointer;

}
.navcart button:active{
    background: #f3f3f3;
}
.navcount{
    width:22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: red;
    color: white;

}
